import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import { useHistory } from 'react-router-dom';
import { Helmet } from "react-helmet-async";
import DashboardLayout from "../../layout/DashboardLayout";
import HeaderNew from "../../components/HeaderNew";
import TopSection from "../../components/TopSection";
import CTAButtons from "../../components/CTAButtons";
// import { Link } from "react-router-dom";
import { Form, Input, Select, Button, Radio, DatePicker } from 'antd';
import { RadioChangeEvent } from "antd/lib/radio/interface";
// import 'moment/locale/zh-cn';
// import locale from 'antd/es/date-picker/locale/zh_CN';
import { 
  getBirthplaceCitiesRequest,  
  setDataFieldPers1,
  setBirthplaceCityId,
} from 'actions/personal1';
// import './Personal1.scss';

type Props = {  
};


const Personal1: React.FC<Props> = () => {
  const dispatch = useDispatch();
  const [form] = Form.useForm(); 
  let history = useHistory(); 
  interface RootState {
    general:{
      phone_code:any,
    }, 
    personal1: {
      loading: boolean, 
      birthplaceCities: [],     
      data:any,
    }, 
  }
  const [isDisabledSubmitBtn,setDisabledSubmitBtn]=useState(true);  
  const birthplaceCities = useSelector( (state: RootState) => state.personal1.birthplaceCities); 
  const data = useSelector( (state: RootState) => state.personal1.data);  
  const phone_code = useSelector( (state: RootState) => state.general.phone_code); 

  useEffect(() => {    
    if(!Object.keys(birthplaceCities).length){    
      dispatch(getBirthplaceCitiesRequest());
    }
    // return () => {dispatch(resetPersonal1Page());};
  }, []);
  useEffect(() => {    
    checkDisableSubmitBtn();    
  }, [data]);

  let progress = {
    step:6,
    total:10
  }
  const submitForm = ()=>{
    console.log("Personal1 PAGE submit");    
    console.log(form.getFieldsValue());    
    history.push('/personal2');
  }  
  // const onFinish = (values:any) => {
  //   console.log('Finish/Submit data:', values);
  // };  
  const checkDisableSubmitBtn = ()=>{           
    Object.values(form.getFieldsValue()).filter(el => !el).length ? setDisabledSubmitBtn(true) : setDisabledSubmitBtn(false);
  }
  
  return (  
    <React.Fragment>
      <Helmet>
        <title>Mekar.id - Personal1</title>
      </Helmet>
      <HeaderNew
        progress={progress}
      />
      <DashboardLayout>
        <React.Fragment>
          <TopSection
            title="Informasi Pribadi"
            info="Masukkan informasi pribadi untuk melengkapi registrasi."
            progress={progress}
          />
          <Form 
            name="personal1-form"
            form={form}
            fields={[
              {name: 'fullname',value:data.fullname},
              {name: 'phone',value: data.phone},
              {name: 'gender',value:data.gender},
              {name: 'birthplace_city_id',value:data.birthplace_city_id&&data.birthplace_city_id.name},
              {name: 'date_of_birth',value: data.date_of_birth&&moment(data.date_of_birth)},
              {name: 'mother_name',value: data.mother_name},
            ]}
            layout='vertical'
            // onFinish={onFinish}
          > 
            <div className="form-items-container">
              <Form.Item
                className="mekar-form-item"
                label="Nama Lengkap sesuai KTP"
                name="fullname" 
                colon={false}                
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input fullname!',
                //   },
                // ]}
              >
                <Input
                  placeholder="Full name"
                  name="fullname"
                  onChange={(e:any)=>dispatch(setDataFieldPers1({
                    name: e.target.name,
                    value: e.target.value
                    }))   
                  } 
                />
              </Form.Item>
              <Form.Item
                className="mekar-form-item"
                label="Nomor Telepon"
                name="phone" 
                colon={false}
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input phone!',
                //   },
                // ]}
              >
                <Input
                  type="text"
                  placeholder="0000-0000-0000"
                  addonBefore={phone_code}
                  onKeyDown={e => {                    
                    if(e.key === 'Delete' || e.key === 'Backspace' || e.key === 'ArrowRight' || e.key === 'ArrowLeft') return
                    !/[0-9]/.test(e.key) && e.preventDefault()
                  }}
                  name="phone"
                  onChange={(e:any)=>dispatch(setDataFieldPers1({
                    name: e.target.name,
                    value: e.target.value
                    }))   
                  } 
                />
              </Form.Item>
              <Form.Item
                className="mekar-form-item"
                label="Jenis Kelamin"
                name="gender" 
                colon={false}
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input gender!',
                //   },
                // ]}
              >
                <Radio.Group                  
                  onChange={(e:any)=>dispatch(setDataFieldPers1({
                    name: e.target.name,
                    value: e.target.value
                    }))   
                  }                   
                  name="gender"
                >
                  <Radio value='male'>Pria</Radio>
                  <Radio value='female'>Wanita</Radio>                  
                </Radio.Group>
              </Form.Item>
              <Form.Item
                className="mekar-form-item"
                label="Tempat Lahir"
                name="birthplace_city_id" 
                colon={false}
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input kodepos!',
                //   },
                // ]}
              >
                <Select                   
                  placeholder="Pilih tempat lahir Anda"
                  options={birthplaceCities.map( (el:any)=>({'lable': el.name, 'value': el.name, 'id': el.code}) )}
                  onSelect={(e,option)=>{                                                                   
                    dispatch(setBirthplaceCityId({
                      id: option.id,name: option.value,
                    }))
                  }}
                  disabled={!birthplaceCities.length}  
                  // optionFilterProp="children"
                  showSearch
                  filterOption={(input, option) => {                      
                      return (option?option.value.toLowerCase().indexOf(input.toLowerCase()) >= 0:false)                      
                    }
                  }
                />
              </Form.Item>
              <Form.Item
                className="mekar-form-item"
                label="Tanggal Lahir"
                name="date_of_birth" 
                colon={false}
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input provinsi!',
                //   },
                // ]}
              >                    
                <DatePicker 
                  // locale={locale}
                  onChange={(e:any)=>dispatch(setDataFieldPers1({                    
                      name: 'date_of_birth',
                      value: e._d,  
                    }))
                  } 
                  placeholder="Pilih tanggal lahir Anda"
                  format="DD/MM/YYYY"
                  style={{display: 'flex', width: '100%'}}
                />
              </Form.Item>
              <Form.Item
                className="mekar-form-item"
                label="Nama Ibu Kandung"
                name="mother_name" 
                colon={false}
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input kota!',
                //   },
                // ]}
              >                    
                <Input 
                  placeholder="Ketik nama ibu kandung  Anda"
                  name="mother_name" 
                  onChange={(e:any)=>dispatch(setDataFieldPers1({
                    name: e.target.name,
                    value: e.target.value
                    }))   
                  } 
                />
              </Form.Item> 
            </div>
            <CTAButtons
              jcAlign="right"
            >          
              <Button
                className="mekar-btn mob-100"
                type="primary"
                disabled={isDisabledSubmitBtn}
                onClick={submitForm}
                htmlType="submit"
              >
                Lanjutkan
              </Button>
            </CTAButtons>
          </Form>
        </React.Fragment>
      </DashboardLayout>
    </React.Fragment> 
  );
};

export default Personal1;
