import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import { Helmet } from "react-helmet-async";
import { useHistory } from 'react-router-dom';
import DashboardLayout from "../../layout/DashboardLayout";
import HeaderNew from "../../components/HeaderNew";
import TopSection from "../../components/TopSection";
import CTAButtons from "../../components/CTAButtons";
import { Form, Select, Button } from 'antd';
import { 
  getReligionsRequest,
  getMaritalStatusesRequest,
  getEducationLevelsRequest,
  getInvestmentPurposesRequest,
  setReligionId,
  setMaritalStatusId,
  setEducationLevelId,
  setInvestmentPurposeId,
  savePersonalInfoRequest,
} from 'actions/personal2';
// import './Personal2.scss';

type Props = {  
};

const Personal2: React.FC<Props> = () => {
  const dispatch = useDispatch();
  const [form] = Form.useForm(); 
  let history = useHistory(); 
  interface RootState {
    general:{
      user_id:any,
    }, 
    personal2: {
      loading: boolean, 
      religions: [],
      maritalStatuses: [],
      educationLevels: [],
      investmentPurposes: [],     
      data:any,
    },
    personal1: {
      data:any,
    }, 
  }

  const user_id = useSelector( (state: RootState) => state.general.user_id);
  const [isDisabledSubmitBtn,setDisabledSubmitBtn]=useState(true);
  const religions = useSelector( (state: RootState) => state.personal2.religions);
  const maritalStatuses = useSelector( (state: RootState) => state.personal2.maritalStatuses);  
  const educationLevels = useSelector( (state: RootState) => state.personal2.educationLevels);
  const investmentPurposes = useSelector( (state: RootState) => state.personal2.investmentPurposes);
  const data = useSelector( (state: RootState) => state.personal1.data);
  const data2 = useSelector( (state: RootState) => state.personal2.data);

  useEffect(() => {    
    if(!Object.keys(religions).length){    
      dispatch(getReligionsRequest());
    }
    if(!Object.keys(maritalStatuses).length){    
      dispatch(getMaritalStatusesRequest());
    }
    if(!Object.keys(educationLevels).length){    
      dispatch(getEducationLevelsRequest());
    }
    if(!Object.keys(investmentPurposes).length){    
      dispatch(getInvestmentPurposesRequest());
    }
    // return () => {dispatch(resetPersonal1Page());};
  }, []);
  useEffect(() => {    
    checkDisableSubmitBtn();    
  }, [data2]);

  let progress = {
    step:7,
    total:10
  }

  const submitForm = ()=>{
    console.log("Personal2 PAGE submit");
    const senddata = {
      body: {
        user_id,

        fullname: data.fullname,
        phone: data.phone,
        gender: data.gender,
        birthplace_city_id: data.birthplace_city_id.id,
        date_of_birth: data.date_of_birth&&moment(data.date_of_birth).format('DD/MM/YYYY'),
        mother_name: data.mother_name,
        

        religion_id: data2.religion_id.id,
        marital_status_id: data2.marital_status_id.id,
        education_level_id: data2.education_level_id.id,
        investment_purpose_id: data2.investment_purpose_id.id,
      }
      // ,
      // query: {
      //   user_id: user_id,
      // },      
    };

    dispatch(savePersonalInfoRequest(senddata));
    history.push('/occupation');
  }
  // const disabledButton = ()=>{
  //   setDisabledSubmitBtn(true);    
  // }
  // const onFinish = (values:any) => {
  //   console.log('Finish/Submit data:', values);
  // };  
  const checkDisableSubmitBtn = ()=>{           
    Object.values(form.getFieldsValue()).filter(el => !el).length ? setDisabledSubmitBtn(true) : setDisabledSubmitBtn(false);
  }

  return (  
    <React.Fragment>
      <Helmet>
        <title>Mekar.id - Personal2</title>
      </Helmet>  
      <HeaderNew
        progress={progress}
      /> 
      <DashboardLayout>   
        <React.Fragment>                  
          <TopSection
            title="Informasi Pribadi"
            info="Masukkan informasi pribadi untuk melengkapi registrasi."
            progress={progress}
          />                        
          <Form 
            name="personal2-form"
            form={form}
            layout='vertical'
            fields={[
              {name: 'religion_id',value:data2.religion_id&&data2.religion_id.name},
              {name: 'marital_status_id',value:data2.marital_status_id&&data2.marital_status_id.name},
              {name: 'education_level_id',value:data2.education_level_id&&data2.education_level_id.name},
              {name: 'investment_purpose_id',value:data2.investment_purpose_id&&data2.investment_purpose_id.name},
            ]}
            // onFinish={onFinish}
          > 
            <div className="form-items-container">              
              <Form.Item
                className="mekar-form-item"
                label="Agama"
                name="religion_id" 
                colon={false}
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input kodepos!',
                //   },
                // ]}
              >
                <Select 
                  placeholder="Pilih agama Anda"
                  // options={dummyArray}
                  options={religions.map( (el:any)=>({'lable': el.name_en, 'value': el.name, 'id': el.id, 'code': el.code}) )}
                  onSelect={(e,option)=>{                                                                   
                    dispatch(setReligionId({
                      id: option.id, name: option.value,
                    }))
                  }}
                  disabled={!religions.length}
                />
              </Form.Item> 
              <Form.Item
                className="mekar-form-item"
                label="Status Pernikahan"
                name="marital_status_id" 
                colon={false}
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input kodepos!',
                //   },
                // ]}
              >
                <Select 
                  placeholder="Pilih status pernikahan Anda"
                  // options={dummyArray}
                  options={maritalStatuses.map( (el:any)=>({'lable': el.name_en, 'value': el.name, 'id': el.id, 'code': el.code}) )}
                  onSelect={(e,option)=>{                                                                   
                    dispatch(setMaritalStatusId({
                      id: option.id, name: option.value,
                    }))
                  }}
                  disabled={!maritalStatuses.length}
                />
              </Form.Item> 
              <Form.Item
                className="mekar-form-item"
                label="Pendidikan"
                name="education_level_id" 
                colon={false}
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input kodepos!',
                //   },
                // ]}
              >
                <Select 
                  placeholder="Pilih pendidikan Anda"
                  // options={dummyArray}
                  options={educationLevels.map( (el:any)=>({'lable': el.name_en, 'value': el.name, 'id': el.id, 'code': el.code, 'hidden': el.hidden}) )}
                  onSelect={(e,option)=>{                                                                   
                    dispatch(setEducationLevelId({
                      id: option.id, name: option.value,
                    }))
                  }}
                  disabled={!educationLevels.length}
                />
              </Form.Item> 
              <Form.Item
                className="mekar-form-item"
                label="Tujuan Investasi"
                name="investment_purpose_id" 
                colon={false}
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input kodepos!',
                //   },
                // ]}
              >
                <Select 
                  placeholder="Pilih tujuan investasi Anda"
                  // options={dummyArray}
                  options={investmentPurposes.map( (el:any)=>({'lable': el.name_en, 'value': el.name, 'id': el.id, 'code': el.code}) )}
                  onSelect={(e,option)=>{                                                                   
                    dispatch(setInvestmentPurposeId({
                      id: option.id, name: option.value,
                    }))
                  }}
                  disabled={!investmentPurposes.length}
                />
              </Form.Item>
            </div>
            <CTAButtons
              jcAlign="right"
            >          
              <Button
                className="mekar-btn mob-100"
                type="primary"
                disabled={isDisabledSubmitBtn}
                onClick={submitForm}
                htmlType="submit"
              >
                Lanjutkan
              </Button>
            </CTAButtons>
          </Form>
        </React.Fragment>
      </DashboardLayout> 
    </React.Fragment>
  );
};

export default Personal2;
