import React from "react";
import StepCounter from "../StepCounter";
import { Progress } from 'antd';
import './TopSection.scss';

type Props = {
  title: string,
  info: string,
  progress: {
    step: number,
    total: number
  },
};

const TopSection: React.FC<Props> = ({ title, info, progress }) => {
  // useEffect(() => {  
  //   console.log("TopSection==>> title:",title,"info:",info,"progress:",progress);   
  //   return () => {};
  // },[]);
  return (
    <div className="TopSection">      
      <h1 className="TopSection-title">{title}</h1>
      <div className="info-container">
        <div className="counter-container">
          <StepCounter
            step={progress.step}
            total={progress.total}
          />
        </div>
        <p className="TopSection-info">{info}</p>
      </div>
      <Progress
        className="MekarProgress"
        showInfo={false}
        percent={progress.step * 10}
        strokeColor="#4A91E1"
        strokeWidth={4}
        // trailColor="#E4EAF6" //used :before  
        trailColor="transparent" 
      />
    </div>
  );
};

export default TopSection;
