import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { Helmet } from "react-helmet-async";
import DashboardLayout from "../../layout/DashboardLayout";
import HeaderNew from "../../components/HeaderNew";
import TopSection from "../../components/TopSection";
import CTAButtons from "../../components/CTAButtons";
// import { Link } from "react-router-dom";
import { Form, Input, Select, Button, Row, Col, Checkbox } from 'antd';
import { 
  getAddressConfigRequest,  
} from 'actions/general';
import {   
  getProvincesHomeRequest,
  setProvinceIdHome,
  getCitiesHomeRequest,
  setCityIdHome,
  getDistrictsHomeRequest,
  setDistrictIdHome,
  getSubdistrictsHomeRequest,
  setSubdistrictIdHome,
  setDataFieldHome,
  copySameKTP,
  getHomestatusesRequest,
  setHomestatusId,
  saveAddressesRequest,
} from 'actions/addressHome';

// import './AddressHome.scss';

type Props = {  
};

const AddressHome: React.FC<Props> = () => {
  const dispatch = useDispatch();
  const [form] = Form.useForm(); 
  let history = useHistory();
  const [isDisabledSubmitBtn,setDisabledSubmitBtn]=useState(true); 
  const [checkSameKTP,setCheckSameKTP]=useState(false);
  interface RootState {    
    addressHome: {
      loading: boolean,
      provinces_home: [],
      cities_home: [],
      districts_home: [],
      subdistricts_home: [],
      homestatuses: [],
      dataHome:any,
    }, 
    address: {
      data:any,
    },    
    general:{
      profile:{
        addressConfig:any
      },
      user_id:any,
    },   
  }

  // const loading = useSelector( (state: RootState) => state.addressHome.loading);
  const addressConfig = useSelector( (state: RootState) => state.general.profile.addressConfig);  
  const user_id = useSelector( (state: RootState) => state.general.user_id);  

  const provinces_home = useSelector( (state: RootState) => state.addressHome.provinces_home);
  const cities_home = useSelector( (state: RootState) => state.addressHome.cities_home);
  const districts_home = useSelector( (state: RootState) => state.addressHome.districts_home);
  const subdistricts_home = useSelector( (state: RootState) => state.addressHome.subdistricts_home);
  const homestatuses = useSelector( (state: RootState) => state.addressHome.homestatuses);

  const data = useSelector( (state: RootState) => state.address.data);
  const dataHome = useSelector( (state: RootState) => state.addressHome.dataHome); 

  

  useEffect(() => {
    // redirect to address because hasn't filled data
    if(!Object.keys(data).length){
      history.push('/address');
    }    
    if(!Object.keys(addressConfig).length){    
      dispatch(getAddressConfigRequest());
    }
    if(!homestatuses.length){    
      dispatch(getHomestatusesRequest());
    }    
    // return () => {dispatch(resetAddressPage());};
  }, []);

  // useEffect(() => {    
  //   console.log("AddressHome data:>>", dataHome);
  //   console.log("form.getFieldsValue():",form.getFieldsValue());
  // });

  useEffect(() => {
    if(addressConfig&&Object.keys(addressConfig).length && !provinces_home.length){
      dispatch(getProvincesHomeRequest(addressConfig.Provinsi.url));
    }
  }, [addressConfig]);

  useEffect(() => {
    if(dataHome.province_id_home){          
      let url = addressConfig['Kota / Kabupaten'].url;
      let requestPoint = `${url.protocol}://${url.host.join('.')}/${url.path.join('/')}?${url.query[0].key}=${dataHome.province_id_home.id}`;      
      dispatch(getCitiesHomeRequest(requestPoint));
    }
  }, [dataHome.province_id_home]);

  useEffect(() => {
    if(dataHome.city_id_home){
      let url = addressConfig.Kecamatan.url;
      let requestPoint = `${url.protocol}://${url.host.join('.')}/${url.path.join('/')}?${url.query[0].key}=${dataHome.city_id_home.id}`;      
      dispatch(getDistrictsHomeRequest(requestPoint));
    }
  }, [dataHome.city_id_home]);

  useEffect(() => {
    if(dataHome.district_id_home){
      let url = addressConfig.Kelurahan.url;
      let requestPoint = `${url.protocol}://${url.host.join('.')}/${url.path.join('/')}?${url.query[0].key}=${dataHome.district_id_home.id}`;      
      dispatch(getSubdistrictsHomeRequest(requestPoint));
    }
  }, [dataHome.district_id_home]);

  useEffect(() => {
    checkDisableSubmitBtn();    
  }, [dataHome]);

  let progress = {
    step:5,
    total:10
  }

  const submitForm = ()=>{
    console.log("AddressHome PAGE submit");
    // console.log(form.getFieldsValue()); 
    // body: {
    //   user_id,

    //   address: "abcdef",
    //   rt: "010",
    //   rw: "011",
    //   postal_code: "10000",
    //   province_id: 11,
    //   city_id: 1101,
    //   district_id: 1101010,
    //   subdistrict_id: 1101010001,

    //   address_home: "abcxyz",
    //   rt_home: "002",
    //   rw_home: "003",
    //   postal_code_home: "20000",
    //   province_id_home: 11,
    //   city_id_home: 1101,
    //   district_id_home: 1101010,
    //   subdistrict_id_home: 1101010001,
    //   homestatus_id: "1"        
    // }
    const senddata = {
      body: {
        user_id,

        address:data.address,
        rt:data.rt,
        rw:data.rw,
        postal_code:data.postal_code,
        province_id: data.province_id.id,
        city_id:data.city_id.id,
        district_id:data.district_id.id,
        subdistrict_id:data.subdistrict_id.id,

        address_home:dataHome.address_home,
        rt_home:dataHome.rt_home,
        rw_home:dataHome.rw_home,
        postal_code_home:dataHome.postal_code_home,
        province_id_home: dataHome.province_id_home.id,
        city_id_home:dataHome.city_id_home.id,
        district_id_home:dataHome.district_id_home.id,
        subdistrict_id_home:dataHome.subdistrict_id_home.id,
        homestatus_id: dataHome.homestatus_id.id,
      }
      // ,
      // query: {
      //   user_id: user_id,
      // },      
    };

    dispatch(saveAddressesRequest(senddata));  
    history.push('/personal1');
  }
  function onChangeCheckSameKTP(e:any) {    
    dispatch(copySameKTP(data));
    setCheckSameKTP(e.target.checked);
  }
  const checkDisableSubmitBtn = ()=>{
    Object.values(form.getFieldsValue()).filter(el => !el).length ? setDisabledSubmitBtn(true) : setDisabledSubmitBtn(false);
  }

  return (    
    <React.Fragment>
      <Helmet>
        <title>Mekar.id - AddressHome</title>
      </Helmet>  
      <HeaderNew
        progress={progress}
      />
      <DashboardLayout>
        <React.Fragment>                   
          <TopSection
            title="Alamat Domisili"
            info="Masukkan alamat domisili untuk melengkapi registrasi."
            progress={progress}
          />                                     
          <Form 
            name="addressHome-form"
            form={form}
            fields={[
              {name: 'address_home',value:dataHome.address_home},
              {name: 'rt_home',value:dataHome.rt_home},
              {name: 'rw_home',value:dataHome.rw_home},
              {name: 'postal_code_home',value:dataHome.postal_code_home},

              {name: 'province_id_home',value: dataHome.province_id_home&&dataHome.province_id_home.name},
              {name: 'city_id_home',value: dataHome.city_id_home&&dataHome.city_id_home.name},
              {name: 'district_id_home',value: dataHome.district_id_home&&dataHome.district_id_home.name},
              {name: 'subdistrict_id_home',value: dataHome.subdistrict_id_home&&dataHome.subdistrict_id_home.name},
              {name: 'homestatus_id',value: dataHome.homestatus_id&&dataHome.homestatus_id.name}
            ]}
            layout='vertical'            
            // onFinish={onFinish}
          > 
            <div className="form-items-container"> 
              <Form.Item
                className="mekar-form-item"                
              >
                <Checkbox
                  onChange={onChangeCheckSameKTP}
                >
                  Ceklis disini jika alamat domisili Anda sama dengan alamat di KTP
                </Checkbox>                
              </Form.Item>
                
              <Form.Item
                className="mekar-form-item"
                label="Alamat"
                name="address_home" 
                colon={false}                
                rules={[
                  {
                    required: true,
                    message: 'Please input alamat!',
                  },
                ]}
              >
                <Input
                  placeholder="Ketik alamat Anda"
                  name="address_home"           
                  onChange={(e:any)=>dispatch(setDataFieldHome({
                    name: e.target.name,
                    value: e.target.value
                    }))   
                  }  
                  disabled={checkSameKTP}
                />
              </Form.Item>
              <Row gutter={[18,0]}>
                <Col flex={'1 1 29%'}>
                  <Form.Item
                    className="mekar-form-item"
                    label="RT"
                    name="rt_home" 
                    colon={false}
                    rules={[
                      {
                        required: true,
                        message: 'Please input RT!',
                      },
                    ]}
                  >
                    <Input
                      type="number"
                      placeholder="000" 
                      onKeyDown={e => {
                        if(e.key === 'Delete' || e.key === 'Backspace' || e.key === 'ArrowRight' || e.key === 'ArrowLeft') return
                        !/[0-9]/.test(e.key) && e.preventDefault()
                      }}
                      name="rt_home"           
                      onChange={(e:any)=>dispatch(setDataFieldHome({
                        name: e.target.name,
                        value: e.target.value
                        }))   
                      }
                      disabled={checkSameKTP} 
                    />
                  </Form.Item>
                </Col>
                <Col flex={'1 1 29%'}>
                  <Form.Item
                    className="mekar-form-item"
                    label="RW"
                    name="rw_home" 
                    colon={false}
                    rules={[
                      {
                        required: true,
                        message: 'Please input RW!',
                      },
                    ]}
                  >
                    <Input
                      type="number"
                      placeholder="000"                      
                      onKeyDown={e => {
                        if(e.key === 'Delete' || e.key === 'Backspace' || e.key === 'ArrowRight' || e.key === 'ArrowLeft') return
                        !/[0-9]/.test(e.key) && e.preventDefault()
                      }}
                      name="rw_home"           
                      onChange={(e:any)=>dispatch(setDataFieldHome({
                        name: e.target.name,
                        value: e.target.value
                        }))   
                      }
                      disabled={checkSameKTP}
                    />
                  </Form.Item>
                </Col>
                <Col flex={'10 1 42%'}>
                  <Form.Item
                    className="mekar-form-item"
                    label="Kodepos"
                    name="postal_code_home" 
                    colon={false}
                    rules={[
                      {
                        required: true,
                        message: 'Please input kodepos!',
                      },
                    ]}
                  >
                    <Input
                      type="number"
                      placeholder="00000"                      
                      onKeyDown={e => {
                        if(e.key === 'Delete' || e.key === 'Backspace' || e.key === 'ArrowRight' || e.key === 'ArrowLeft') return
                        !/[0-9]/.test(e.key) && e.preventDefault()
                      }}
                      name="postal_code_home"           
                      onChange={(e:any)=>dispatch(setDataFieldHome({
                        name: e.target.name,
                        value: e.target.value
                        }))   
                      }
                      disabled={checkSameKTP}
                    />
                  </Form.Item>
                </Col>
              </Row>   
              <Row gutter={[18,0]}>
                <Col span={12}>       
                  <Form.Item
                    className="mekar-form-item"
                    label="Provinsi"
                    name="province_id_home" 
                    colon={false}
                    // rules={[
                    //   {
                    //     required: true,
                    //     message: 'Please input provinsi!',
                    //   },
                    // ]}
                  >                    
                    <Select 
                      placeholder="Ketik provinsi Anda"
                      options={provinces_home.map( (el:any)=>({'lable': el.nama, 'value': el.nama, 'id': el.id}) )}
                      onSelect={(e,option)=>{                                                
                        dispatch(setProvinceIdHome({
                          id: option.id,name: option.value,
                        }))
                      }} 
                      disabled={checkSameKTP||!provinces_home.length}
                    />
                  </Form.Item>
                </Col>  
                <Col span={12}>
                  <Form.Item
                    className="mekar-form-item"
                    label="Kota"
                    name="city_id_home"
                    colon={false}
                    // rules={[
                    //   {
                    //     required: true,
                    //     message: 'Please input kota!',
                    //   },
                    // ]}
                  >                    
                    <Select 
                      placeholder="Ketik kota Anda"
                      options={cities_home.map( (el:any)=>({'lable': el.nama, 'value': el.nama, 'id': el.id}) )}
                      onSelect={(e,option)=>dispatch(setCityIdHome({
                        id: option.id,name: option.value,
                      }))}  
                      disabled={checkSameKTP||!cities_home.length}
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={[18,0]}>
                <Col span={12}> 
                  <Form.Item
                    className="mekar-form-item"
                    label="Kecamatan"
                    name="district_id_home" 
                    colon={false}
                    // rules={[
                    //   {
                    //     required: true,
                    //     message: 'Please input kecamatan!',
                    //   },
                    // ]}
                  >                    
                    <Select 
                      placeholder="Ketik kecamatan Anda"
                      options={districts_home.map( (el:any)=>({'lable': el.nama, 'value': el.nama, 'id': el.id}) )}
                      onSelect={(e,option)=>dispatch(setDistrictIdHome({
                        id: option.id,name: option.value,
                      }))} 
                      disabled={checkSameKTP||!districts_home.length}
                    />
                  </Form.Item>
                </Col>  
                <Col span={12}>
                  <Form.Item
                    className="mekar-form-item"
                    label="Kelurahan"
                    name="subdistrict_id_home" 
                    colon={false}
                    // rules={[
                    //   {
                    //     required: true,
                    //     message: 'Please input kelurahan!',
                    //   },
                    // ]}
                  >
                    <Select 
                      placeholder="Ketik kelurahan Anda"
                      options={subdistricts_home.map( (el:any)=>({'lable': el.nama, 'value': el.nama, 'id': el.id}) )}
                      onSelect={(e,option)=>dispatch(setSubdistrictIdHome({
                        id: option.id,name: option.value,
                      }))} 
                      disabled={checkSameKTP||!subdistricts_home.length}
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Form.Item
                className="mekar-form-item"
                label="Status Kepemilikan Domisili"
                name="homestatus_id" 
                colon={false}
                // rules={[
                //   {
                //     required: true,
                //     message: 'Please input Status Kepemilikan Domisili!',
                //   },
                // ]}
              >
                <Select                   
                  placeholder="Pilih status kepemilikan domisili Anda"
                  options={homestatuses.map( (el:any)=>({'lable': el.code, 'lable_en': el.name_en, 'value': el.name, 'id': el.id}) )}
                  disabled={!homestatuses.length}
                  onSelect={(e,option)=>dispatch(setHomestatusId({
                    id: option.id,
                    name: option.value,
                  }))} 
                />
              </Form.Item>
            </div>
            <CTAButtons
              jcAlign="right"
            >          
              <Button
                className="mekar-btn mob-100"
                type="primary"
                disabled={isDisabledSubmitBtn}
                onClick={submitForm}
                htmlType="submit"
              >
                Lanjutkan
              </Button>
            </CTAButtons>
          </Form>
          {/* <Link to="/address" style={{"marginRight":'10px'}}>Address</Link>
          <Link to="/address-home" style={{"marginRight":'10px'}}>AddressHome</Link>
          <Link to="/personal1" style={{"marginRight":'10px'}}>Personal1</Link>
          <Link to="/personal2" style={{"marginRight":'10px'}}>Personal2</Link>
          <Link to="/occupation" style={{"marginRight":'10px'}}>Occupation</Link>
          <Link to="/tax" style={{"marginRight":'10px'}}>Tax</Link>
          <Link to="/bank" style={{"marginRight":'10px'}}>Bank</Link>
          <Link to="/thankyounew" style={{"marginRight":'10px'}}>Thankyounew</Link> */}
        </React.Fragment>        
      </DashboardLayout>
    </React.Fragment>
  );
};

export default AddressHome;
