import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { Helmet } from "react-helmet-async";
import DashboardLayout from "../../layout/DashboardLayout";
import HeaderNew from "../../components/HeaderNew";
import TopSection from "../../components/TopSection";
import CTAButtons from "../../components/CTAButtons";
// import { Link } from "react-router-dom";
import { Form, Input, Select, Button, Row, Col } from 'antd';
import { 
  getAddressConfigRequest,  
} from 'actions/general';
import {   
  getProvincesRequest,
  setProvinceId,
  getCitiesRequest,
  setCityId,
  getDistrictsRequest,
  setDistrictId,
  getSubdistrictsRequest,
  setSubdistrictId,
  setDataField,
} from 'actions/address';

// import './Address.scss';

type Props = {  
};

const Address: React.FC<Props> = () => {
  const dispatch = useDispatch();
  const [form] = Form.useForm(); 
  let history = useHistory();
  const [isDisabledSubmitBtn,setDisabledSubmitBtn]=useState(true);  
  interface RootState {
    address: {
      loading: boolean,
      provinces: [],
      cities: [],
      districts: [],
      subdistricts: [],
      data:any,
    },    
    general:{
      profile:{
        addressConfig:any
      },
    },
    data:{}
  }
  // const loading = useSelector( (state: RootState) => state.address.loading);
  const addressConfig = useSelector( (state: RootState) => state.general.profile.addressConfig);    

  const provinces = useSelector( (state: RootState) => state.address.provinces);
  const cities = useSelector( (state: RootState) => state.address.cities);
  const districts = useSelector( (state: RootState) => state.address.districts);
  const subdistricts = useSelector( (state: RootState) => state.address.subdistricts);

  const data = useSelector( (state: RootState) => state.address.data);  

  useEffect(() => {    
    if(!Object.keys(addressConfig).length){    
      dispatch(getAddressConfigRequest());
    }
    // return () => {dispatch(resetAddressPage());};
  }, []);

  // useEffect(() => {    
  //   console.log("Address data:>>", data);
  // });

  useEffect(() => {
    if(addressConfig&&Object.keys(addressConfig).length && !provinces.length){
      dispatch(getProvincesRequest(addressConfig.Provinsi.url));
    }
  }, [addressConfig]);

  useEffect(() => {
    if(data.province_id){          
      let url = addressConfig['Kota / Kabupaten'].url;
      let requestPoint = `${url.protocol}://${url.host.join('.')}/${url.path.join('/')}?${url.query[0].key}=${data.province_id.id}`;      
      dispatch(getCitiesRequest(requestPoint));
    }
  }, [data.province_id]);

  useEffect(() => {
    if(data.city_id){
      let url = addressConfig.Kecamatan.url;
      let requestPoint = `${url.protocol}://${url.host.join('.')}/${url.path.join('/')}?${url.query[0].key}=${data.city_id.id}`;      
      dispatch(getDistrictsRequest(requestPoint));
    }
  }, [data.city_id]);

  useEffect(() => {
    if(data.district_id){
      let url = addressConfig.Kelurahan.url;
      let requestPoint = `${url.protocol}://${url.host.join('.')}/${url.path.join('/')}?${url.query[0].key}=${data.district_id.id}`;      
      dispatch(getSubdistrictsRequest(requestPoint));
    }
  }, [data.district_id]);

  useEffect(() => {    
    checkDisableSubmitBtn();    
  }, [data]);
  
  let progress = {
    step:4,
    total:10
  }
  const submitForm = ()=>{
    console.log("Address PAGE submit");
    // console.log(form.getFieldsValue());    
    history.push('/address-home');
  }  
  // const onFinish = (values:any) => {
  //   console.log('Finish/Submit data:', values);
  // };
  const checkDisableSubmitBtn = ()=>{           
    Object.values(form.getFieldsValue()).filter(el => !el).length ? setDisabledSubmitBtn(true) : setDisabledSubmitBtn(false);
  }
  
  return ( 
    <React.Fragment>
      <Helmet>
        <title>Mekar.id - Address</title>
      </Helmet> 
      <HeaderNew
        progress={progress}
      />
      <DashboardLayout>
        <React.Fragment>  
          <TopSection
            title="Alamat KTP"
            info="Masukkan informasi alamat Anda untuk melengkapi registrasi."
            progress={progress}
          />
          <Form 
            name="address-form"
            form={form}
            fields={[
              {name: 'address',value:data.address},
              {name: 'rt',value:data.rt},
              {name: 'rw',value:data.rw},
              {name: 'postal_code',value:data.postal_code},

              {name: 'province_id',value: data.province_id&&data.province_id.name},
              {name: 'city_id',value: data.city_id&&data.city_id.name},
              {name: 'district_id',value: data.district_id&&data.district_id.name},
              {name: 'subdistrict_id',value: data.subdistrict_id&&data.subdistrict_id.name}
            ]}
            layout='vertical'
            // onFinish={onFinish}            
          > 
            <div className="form-items-container"> 
              <Form.Item
                className="mekar-form-item"
                label="Alamat"
                name="address" 
                colon={false}
                rules={[
                  {
                    required: true,
                    message: 'Please input alamat!',
                  },
                ]}
              >
                <Input
                  placeholder="Ketik alamat Anda" 
                  name="address"           
                  onChange={(e:any)=>dispatch(setDataField({
                    name: e.target.name,
                    value: e.target.value
                    }))   
                  }             
                />
              </Form.Item>
              <Row gutter={[18,0]}>
                <Col flex={'1 1 29%'}>
                  <Form.Item
                    className="mekar-form-item"
                    label="RT"
                    name="rt" 
                    colon={false}
                    rules={[
                      {
                        required: true,
                        message: 'Please input RT!',
                      },
                    ]}
                  >
                    <Input
                      type="number"
                      placeholder="000"                      
                      onKeyDown={e => {
                        if(e.key === 'Delete' || e.key === 'Backspace' || e.key === 'ArrowRight' || e.key === 'ArrowLeft') return
                        !/[0-9]/.test(e.key) && e.preventDefault()
                      }}
                      name="rt"           
                      onChange={(e:any)=>dispatch(setDataField({
                        name: e.target.name,
                        value: e.target.value
                        }))   
                      }
                    />
                  </Form.Item>
                </Col>
                <Col flex={'1 1 29%'}>
                  <Form.Item
                    className="mekar-form-item"
                    label="RW"
                    name="rw" 
                    colon={false}
                    rules={[
                      {
                        required: true,
                        message: 'Please input RW!',
                      },
                    ]}
                  >
                    <Input
                      type="number"
                      placeholder="000"
                      onKeyDown={e => {
                        if(e.key === 'Delete' || e.key === 'Backspace' || e.key === 'ArrowRight' || e.key === 'ArrowLeft') return
                        !/[0-9]/.test(e.key) && e.preventDefault()
                      }}
                      name="rw"           
                      onChange={(e:any)=>dispatch(setDataField({
                        name: e.target.name,
                        value: e.target.value
                        }))   
                      }
                    />
                  </Form.Item>
                </Col>
                <Col flex={'10 1 42%'}>
                  <Form.Item
                    className="mekar-form-item"
                    label="Kodepos"
                    name="postal_code" 
                    colon={false}
                    rules={[
                      {
                        required: true,
                        message: 'Please input kodepos!',
                      },
                    ]}
                  >
                    <Input
                      type="number"
                      placeholder="00000"
                      onKeyDown={e => {
                        if(e.key === 'Delete' || e.key === 'Backspace' || e.key === 'ArrowRight' || e.key === 'ArrowLeft') return
                        !/[0-9]/.test(e.key) && e.preventDefault()
                      }}
                      name="postal_code"           
                      onChange={(e:any)=>dispatch(setDataField({
                        name: e.target.name,
                        value: e.target.value
                        }))   
                      }
                    />
                  </Form.Item>
                </Col>
              </Row>   
              <Row gutter={[18,0]}>
                <Col span={12}>       
                  <Form.Item
                    className="mekar-form-item"
                    label="Provinsi"
                    name="province_id" 
                    colon={false}                                     
                    // rules={[
                    //   {
                    //     required: true,
                    //     message: 'Please input provinsi!',
                    //   },
                    // ]}
                  >                    
                    <Select 
                      placeholder="Ketik provinsi Anda"
                      options={
                        provinces.map( (el:any)=>({'lable': el.nama, 'value': el.nama, 'id': el.id}) )
                      }
                      disabled={!provinces.length}                      
                      onSelect={(e,option)=>{                                                
                        dispatch(setProvinceId({
                          id: option.id,name: option.value,
                        }))
                      }}                         
                    />
                  </Form.Item>
                </Col>  
                <Col span={12}>
                  <Form.Item
                    className="mekar-form-item"
                    label="Kota"
                    name="city_id" 
                    colon={false}                    
                    // rules={[
                    //   {
                    //     required: true,
                    //     message: 'Please input kota!',
                    //   },
                    // ]}
                  >                    
                    <Select 
                      placeholder="Ketik kota Anda"
                      options={
                        cities.map( (el:any)=>({'lable': el.nama, 'value': el.nama, 'id': el.id}) )
                      }
                      disabled={!cities.length}                      
                      onSelect={(e,option)=>dispatch(setCityId({
                        id: option.id,name: option.value,
                      }))}                        
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={[18,0]}>
                <Col span={12}> 
                  <Form.Item
                    className="mekar-form-item"
                    label="Kecamatan"
                    name="district_id" 
                    colon={false}                    
                    // rules={[
                    //   {
                    //     required: true,
                    //     message: 'Please input kecamatan!',
                    //   },
                    // ]}
                  >                    
                    <Select 
                      placeholder="Ketik kecamatan Anda"
                      options={
                        districts.map( (el:any)=>({'lable': el.nama, 'value': el.nama, 'id': el.id}) )
                      }
                      disabled={!districts.length}                      
                      onSelect={(e,option)=>dispatch(setDistrictId({
                        id: option.id,name: option.value,
                      }))} 
                    />                    
                  </Form.Item>
                </Col>  
                <Col span={12}>
                  <Form.Item
                    className="mekar-form-item"
                    label="Kelurahan"
                    name="subdistrict_id" 
                    colon={false}                    
                    // rules={[
                    //   {
                    //     required: true,
                    //     message: 'Please input kelurahan!',
                    //   },
                    // ]}
                  >
                    <Select 
                      placeholder="Ketik kelurahan Anda"
                      options={
                        subdistricts.map( (el:any)=>({'lable': el.nama, 'value': el.nama, 'id': el.id}) )
                      }
                      disabled={!subdistricts.length}                      
                      onSelect={(e,option)=>dispatch(setSubdistrictId({
                        id: option.id,name: option.value,
                      }))}                       
                    />                    
                  </Form.Item>
                </Col>
              </Row>
            </div>
            <CTAButtons
              jcAlign="right"
            >          
              <Button
                className="mekar-btn mob-100"
                type="primary"
                disabled={isDisabledSubmitBtn}
                onClick={submitForm}
                htmlType="submit"
              >
                Lanjutkan
              </Button>
            </CTAButtons>
          </Form>
          {/* <Link to="/address" style={{"marginRight":'10px'}}>Address</Link>
          <Link to="/address-home" style={{"marginRight":'10px'}}>AddressHome</Link>
          <Link to="/personal1" style={{"marginRight":'10px'}}>Personal1</Link>
          <Link to="/personal2" style={{"marginRight":'10px'}}>Personal2</Link>
          <Link to="/occupation" style={{"marginRight":'10px'}}>Occupation</Link>
          <Link to="/tax" style={{"marginRight":'10px'}}>Tax</Link>
          <Link to="/bank" style={{"marginRight":'10px'}}>Bank</Link>
          <Link to="/thankyounew" style={{"marginRight":'10px'}}>Thankyounew</Link> */}
        </React.Fragment>
      </DashboardLayout>
    </React.Fragment>
  );
};

export default Address;
